:root {
  --background: #ffffff;
  --foreground: #171717;
  --brand: #0ea5e9;
  --brand-600: #0284c7;
  --muted: #f5f7fb;
  --border: #e6e8ee;
  color-scheme: light;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}
.h1 {
  font-size: 2.25rem;
  line-height: 1.15;
  letter-spacing: -0.02em;
  font-weight: 700;
}
.h2 {
  font-size: 1.5rem;
  line-height: 1.25;
  letter-spacing: -0.01em;
  font-weight: 600;
}
.text-muted {
  color: var(--foreground);
  @supports (color: color-mix(in lab, red, red)) {
    color: color-mix(in oklab, var(--foreground) 60%, transparent);
  }
}
.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
  max-width: 72rem;
}
.bg-dots {
  background-image: radial-gradient(rgba(2, 8, 23, 0.06) 1px, transparent 1px);
  background-size: 24px 24px;
}
.hover-lift {
  transition: transform 200ms ease, box-shadow 200ms ease;
}
.hover-lift:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0,0,0,0.06);
}
.focus-ring:focus {
  outline: none;
  box-shadow: 0 0 0 3px var(--color-brand);
  @supports (color: color-mix(in lab, red, red)) {
    box-shadow: 0 0 0 3px color-mix(in oklab, var(--color-brand) 40%, transparent);
  }
}
